*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.big_end{
    margin-bottom: 5em;
}

.end_p{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 1em;
    font-size: 80px;
}

.end_span{
    display: flex;
    justify-content: start;
    margin: 0 1.3em;
    font-size: 60px;
    font-weight: 900;
}

.end_span_2{
    margin-left: 5.7em;
}

.two_end{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1em 5em;
    gap: 20px;
}

.end_end_p{
    font-size: 20px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.end_arrow{
    margin-top: 10px;
}

.end_arr{
    display: flex;
    justify-content: start;
    align-items: center;
}


