*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.dress{
    display: flex;
    flex-direction: column;
}

.top{
    display: flex;
    justify-content: space-between;
    margin: 3em 4em;
}

.dress_h1{
    font-size: 20px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.dress_btn button{
    border: 0;
    padding: 12px 45px;
    color: white;
    border-radius: 30px;
    background-color: black;
    font-size:medium;
}

.div_a_lot{
    display: flex;
    justify-content: space-between;
    padding: 0 1em;
    margin-bottom: 10px;
}

.two_person{
    display: flex;
    justify-content: center;
}

.left_arr{
    margin-top: 24em;
}

.right_arr{
    margin-top: 24em;
}

.negr_h1{
    font-size: 30px;
    margin-top: 10px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.negr p{
    margin-top: 0.8em;
    color: gray;
    font-size: 12px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.girl_h1{
    font-size: 30px;
    margin-top: 10px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.girl p{
    margin-top: 0.8em;
    color: gray;
    font-size: 12px;    
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

