*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.big_asser{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5em;
    padding: 0 3em;
    margin-bottom: 5em;
}

.asser_left{
    display: flex;
    justify-content: start;
    flex-direction: column;
    margin-right: 12em;
    gap: 40px;}

.asser_h1{
    font-size: 30px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.asser_p{
    font-size: 20px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

button{
    color: white;
    background-color: black;
    bottom: 0;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}