*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.big_nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.2em 3em;
}

.uls ul{
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    gap: 2em;
}

.h1{
    margin-right: 6em;
}

.icons{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3em;
}

@media only screen and (max-width: 440px) {
    .big_nav{
        width: 42rem;

        .uls{
            font-size: 10px;
            font-weight:500;
        }

        .h1{
            font-size: 10px;
        }

        .icons{ 
            gap: 20px;
        }
    }
}