*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.big_rectrec{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5em;
    padding: 0 3em;
    margin-bottom: 5em;
}

.rec_left{
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.rec_h1{
    font-size: 30px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.rec_p{
    font-size: 20px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

button{
    color: white;
    background-color: black;
    padding: 1em 3em;
    border-radius: 20px;
    border: 0;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}