*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.leggings_div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5em;
    padding: 0 3em;
    margin-bottom: 5em;
}

.leg_right{
    display: flex;
    justify-content: start;
    flex-direction: column;
    margin-right: 12em;
    gap: 40px;
}

.leg_h1{
    font-size: 30px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}   

.leg_p{
    font-size: 20px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.lrg_btn{
    color: white;
    background-color: black;
    padding: 1em 3em;
    border-radius: 30px;
    border: 0;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}