*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.blue_nav{
    color: white;
    padding: 10px 0px;
    background-color: rgb(0, 0, 255);
    font-size: 15px;
    text-align: center;
    margin-top: 0;
}

@media screen and (max-width: 550px){
    .blue_nav{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 41.3rem;
    }
}
